<template>
  <div class="login-div">
    <h2 class="title-text"></h2>
    <el-form
      :model="loginForm"
      status-icon
      :rules="rules"
      ref="loginForm"
      label-width="100px"
      class="login-form"
    >
      <el-form-item label="" prop="username">
        <el-input v-model.number="loginForm.username" placeholder=""></el-input>
      </el-form-item>
      <el-form-item label="" prop="password">
        <el-input type="password" @keydown.enter.native="loginAction('loginForm')" v-model="loginForm.password" autocomplete="off" placeholder=""></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="loginAction('loginForm')"></el-button>
        <el-button @click="resetForm('loginForm')"></el-button>
      </el-form-item>
    </el-form>
  </div>
</template>
<style lang="scss" scoped>
  .login-div{
    width: 500px;
    margin: 0 auto;
    margin-top: 150px;
    padding: 20px 30px;
    border: 1px solid #f5f5f5;
    border-radius: 10px;
    .title-text{
      font-size: 30px;
      text-align: center;
      color: #333;
      line-height: 2;
      margin-bottom: 30px;
    }
    .login-form{

    }
  }
</style>
<script>
import axios from "../common/http.js";
export default {
  data() {
    const checkUsername = (rule, value, callback) => {
      if (!/^\d{11}$/.test(value)) {
        return callback(new Error("11"));
      } else {
        callback();
      }
    };
    var validatePassword = (rule, value, callback) => {
      if (value === "") {
        callback(new Error(""));
      } else {
        callback();
      }
    };
    return {
      toURL: this.$route.query.from || '/',
      loginForm: {
        username: "",
        password: ""
      },
      rules: {
        username: [{ validator: checkUsername, trigger: "blur" }],
        password: [{ validator: validatePassword, trigger: "blur" }]
      }
    };
  },
  methods: {
    loginAction(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          axios.post("/admin/loginAdmin", this.loginForm).then((res)=>{
            if(res&&res.code==1){
              this.$message({
                message: '',
                type: 'success',
                showClose: true,
                onClose: ()=>{
                  this.$router.push(this.toURL);//
                }
              })
            }else if(res.tips){
              this.$alert(res.tips, "", {
                confirmButtonText: ""
              });
            }
          });
        } else {
          this.$alert("", "", {
            confirmButtonText: ""
          });
          return false;
        }
      });
    },
    resetForm(formName) {
      this.$refs[formName].resetFields();
    }
  }
};
</script>